import React from 'react';
import { Row, Col, Card, Table, Modal, Button } from 'react-bootstrap';

import Aux from "../../hoc/_Aux";

import IsEmpty from '../../App/components/Reusables/IsEmpty';
import Jhilikka from '../../App/components/Reusables/Jhilikka';
import { getLedgerEntries } from '../../api/accounting/ledger';
import isEmpty from '../../validation/isEmpty';
import Spinner from '../../App/components/Reusables/Spinner';
import Paging from '../../App/components/Reusables/Paging';
import LedgerEntriesFilter from '../../App/components/SearchFilters/LedgerEntriesFilter';
import { getLedgerEntriesSearchFilterObject } from '../../helpers/getSearchFilterObject';
import { getStartEnd } from '../../helpers/getStartEnd';

const LedgerEntries = (props) => {
    const [loading, setLoading] = React.useState(true)
    const [ledgerEntriesArr, setLedgerEntriesArr] = React.useState([]);
    const [ledgerEntriesObj, setLedgerEntriesObj] = React.useState({});
    const [instanceCount, setInstanceCount] = React.useState(0)

    // const match = React.useMatch()

    React.useEffect(() => {
        const controller = new AbortController();
        const requestJson = {
            "action": "get",
            "filter": "none",
            ...getLedgerEntriesSearchFilterObject(props),
            ...getStartEnd(props)
        }
        console.log(JSON.stringify(requestJson))
        getLedgerEntries(requestJson, controller.signal)
            .then((res) => {
                console.log(res);
                if (res.json.status) {
                    setLedgerEntriesArr([...res.json.ledger_entries]);
                    setInstanceCount(res.json.count)
                    setLoading(false);
                } else { Jhilikka.fire({ icon: 'error', title: res.json.error, background: '#ffe5de' }) }
            })
            .catch((err) => {
                console.log(err)
            });

        return () => controller.abort();
    }, []);


    React.useEffect(() => {
        const filtered = {};

        //keep same transactions in same
        for (let en of ledgerEntriesArr) {
            if (filtered[en.bundle_id]) {
                filtered[en.bundle_id] = [...filtered[en.bundle_id], en];
            } else {
                filtered[en.bundle_id] = [en]
            }
        }

        //order by dr and cr
        for (let txn in filtered) {
            const orderedTxn = [];
            for (let entry of filtered[txn]) {
                if (entry.is_add) {
                    orderedTxn.unshift(entry);
                } else {
                    orderedTxn.push(entry)
                }
            }
            filtered[txn] = orderedTxn;
            console.log(filtered[txn])
        }

        setLedgerEntriesObj({ ...filtered })
    }, [JSON.stringify(ledgerEntriesArr)])


    const LedgerEntriesList = Object.keys(ledgerEntriesObj).map((key, ind) => {
        const invoice = ledgerEntriesObj[key][0].payment[0].invoice;
        const purchaseOrder = ledgerEntriesObj[key][0].payment[0].purchase_order;

        const remarksClickLink = invoice || purchaseOrder ?
            invoice ? `/invoice/${invoice}` : `/purchase-order/${purchaseOrder}` :
            null


        console.log("Purchase order link is", remarksClickLink)

        return ([
            <tr key={ind + 'p'} >
                <th colSpan={2}>
                    {key}
                </th>
                <th colSpan={4} className="text-right">
                    {new Date(ledgerEntriesObj[key][0].date).toDateString()}
                </th>
            </tr>,
            ledgerEntriesObj[key].map((en, index) => {
                return (
                    <tr key={index} className="journalEntriesList">
                        <th>{en.id}</th>
                        <td
                            onClick={() => {
                                props.history.push('/account/' + en.account_uuid)
                            }}
                            style={{ cursor: 'pointer', color: '#55588b' }}
                        >
                            <span style={{ color: '#888888' }}>{en.is_add ? '' : 'To '}</span>{en.account_str} {" "}({en.account_type_str})
                        </td>
                        <td> {en.is_add ? 'Rs. ' + en.payment[0].amount.toLocaleString() : null} </td>
                        <td>{en.is_add ? null : 'Rs. ' + en.payment[0].amount.toLocaleString()} </td>
                    </tr>
                )
            }),
            <tr key={ind + 'x'} className="journalEntriesList">
                <td colSpan={1} style={{ borderBottom: '3px solid silver' }}></td>
                <td onClick={remarksClickLink ? () => props.history.push(remarksClickLink) : () => { }} colSpan={5} style={{ borderBottom: '3px solid silver', cursor: 'pointer' }}>
                    <b></b> {" "}{ledgerEntriesObj[key][0].remarks}
                </td>
            </tr>,

        ])



    })



    return (
        <Aux>
            <Row className="d-flex justify-content-between pageHead" >
                <h4 className="m-b-10">Ledger Entries</h4>
                <div>
                    <button
                        className="btn btn-primary btn-sm shadow-2 mb-4"
                        onClick={() => props.history.push(`ledger-entry/create`)}
                    > <i className="fa fa-plus text-c-white f-15 m-r-5" />Add New</button>
                </div>
            </Row>
            <Row>
                <Col>

                    <Card>
                        <Card.Body>
                            <LedgerEntriesFilter />
                            <Table responsive>
                                <thead>
                                    <tr>
                                        <th>#</th>
                                        <th>Account</th>
                                        <th>Debits</th>
                                        <th>Credits</th>
                                        {/* <th>Account Header</th> */}
                                    </tr>
                                </thead>
                                <tbody>
                                    {LedgerEntriesList || ''}
                                </tbody>
                            </Table>
                            {isEmpty(ledgerEntriesArr) && !loading ? <><IsEmpty>Data not available</IsEmpty><br /><br /></> : ''}
                            {loading ? <Spinner /> : null}
                            <Paging instanceCount={instanceCount} />
                        </Card.Body>

                    </Card >


                </Col>
            </Row>
        </Aux>
    );
}

export default LedgerEntries;